.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    background: transparent;
    font-family: 'Roboto', sans-serif;
    transition: background 0.3s ease;
    font-size: 1.5rem;
    height: 10vh;
  }
  
  .logo {
    position: absolute;
    left: 20px;
  }
  
  .logo img {
    height: 50px;
    position: absolute;
    top: -2vw;
    transition: opacity 1s ease;
  }
  
  .logo .logo-white {
    opacity: 0;
  }
  
  .navbar:hover .logo .logo-black {
    opacity: 0;
  }
  
  .navbar:hover .logo .logo-white {
    opacity: 1;
  }
  
  .navbar:hover {
    background: #004d00;
  }
  
  .navbar.hide {
    opacity: 0;
    background-color: rgba(1, 138, 19, 0);
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 40px;
    align-items: center;
  }
  .nav-links a
  {
    text-decoration: none;
  }
  .nav-links li {
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .nav-links li:hover {
    position: relative;
    background-color: rgb(4, 60, 0);
    height: 6.4vw;
    width: 10vw;
    transition: background-color 0.5s ease;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .dropdown {
    position: relative;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #018a13;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    overflow: hidden;
    z-index: 1000;
    transition: opacity 0.3s ease, transform 0.3s ease, visibility 0s ease 0.3s;
    opacity: 0;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
    opacity: 1;
  }
  
  .nav-links li button {
    color: #fff;
    text-decoration: none;  /* Ensure no underline */
    transition: color 0.5s ease;
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px 20px;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .nav-links li button:hover {
    color: #b3e6ff;
  }
  
  .nav-links .dropdown > button {
    display: flex;
    align-items: center;
  }
  
  .nav-links .dropdown > button > svg {
    margin-left: 8px;
    transition: transform 0.3s ease;
  }
  
  .nav-links .dropdown:hover > button > svg {
    transform: rotate(-90deg);
  }
  
  .dropdown-content button {
    display: block;
    width: 100%;
    padding: 10px 20px;
    text-align: left;
    background: none;
    border: none;
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    border-bottom: 1px solid #005a30;
    text-decoration: none;
  }
  
  .dropdown-content button:hover {
    background-color: #003808;
  }
  
  .dropdown-content button:last-child {
    border-bottom: none;
  }
  
  .dropdown-arrow {
    position: absolute;
    top: -1.4vw;
    left: -0.3vw;
    font-size: 3rem;
    color: rgb(0, 60, 23);
  }
  
  .lang-container {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .lang-container button {
    color: #fff;
    padding: 8px 16px;
    transition: background 0.3s;
    display: flex;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
  }
  
  .lang-container button:hover {
    background-color: #003808;
    border-radius: 4px;
  }
  
  .lang-container button img {
    margin-right: 8px;
    width: 5vh;
    height: auto;
  }
  
  .lang-dropdown {
    display: none;
    position: absolute;
    top: 100%;
    background-color: #018a13;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    overflow: hidden;
    z-index: 1000;
    opacity: 0;
    transition: opacity 0.3s ease, max-height 0.3s ease;
    max-height: 0;
    justify-content: center;
  }
  
  .lang-container:hover .lang-dropdown {
    display: block;
    opacity: 1;
    max-height: 250vw;
  }
  
  .lang-dropdown button {
    display: block;
    padding: 8px 8px;
    color: #fff;
    text-decoration: none;  /* Ensure no underline */
    white-space: nowrap;
    transition: background 0.3s;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .lang-dropdown button:hover {
    background-color: #003808;
  }
  
  .menu-icon {
    display: none;
    font-size: 2rem;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .nav-links {
        display: none;
    }
    .navbar.hide
    {
      opacity: 1;
      background: #004d00;
    }
    .navbar
    {
      background: #004d00;
    }
    .menu-icon {
        display: block;
        font-size: 2rem;
        cursor: pointer;
        position: absolute;
        top: 2rem;
        right: 3rem;
    }

    .dropdown-arrow {
        opacity: 0;
    }

    .nav-links.open {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 20vw;
        right: 0;
        background-color: #018a13;
        width: 60%;
        max-width: 60vw;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        z-index: 1000;
    }
    .nav-links li:hover {
      background:none;
    }
    .nav-links li {
        display: flex;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        width: 130%;
    }

    .nav-links li button {
        display:block;
        width: 100%;
        text-align: left;
        padding: 1 rem;
        background: none;
        border: none;
        color: white;
        font-size: 5vw;
        cursor: pointer;
    }

    .nav-links li .dropdown-content {
        display: none;
        background-color: #00b755;
        left: -34.5vw;
        width: 100vw;
        box-sizing: border-box;
        
    }

    .nav-links li .dropdown-content button {
        display: flexbox;
        justify-content: left;
        width: 100vw;
        text-align: left;
        background: none;
        border: none;
        color: white;
        font-size: 5vw;
        cursor: pointer;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        box-sizing: border-box;
    }

    .nav-links li .dropdown-content button:hover {
        background-color: rgba(0, 0, 0, 0.1);
    }

    .nav-links li .dropdown-content.show {
        display: block;
    }

    .lang-container button {
        background: none;
        border: none;
        color: white;
        cursor: pointer;
    }
}